const createMenu = function () {
  const createMenuTemplate = (
    idMenuItem,
    textMenu,
    adClass = '',
    isChecked = false) => {
    return `<input type="radio"
				name="control"
				id="control__${idMenuItem}"
				class="control__input visually-hidden"
				${isChecked ? 'checked' : ''}	/>
				<label for="control__${idMenuItem}" class="control__label ${adClass}">${textMenu}</label>
			`;
  };

  return `<section class="control__btn-wrap">
		${createMenuTemplate('new-task', '+ ADD NEW TASK', 'control__label--new-task')}
		${createMenuTemplate('task', 'TASKS', '', true)}
		${createMenuTemplate('statistic', 'STATISTICS', '')}
		</section>
		`;
};

export { createMenu };
