//Фильтры
const createFilters = () => {
  const createFiltersItemTemplate = (
    filterId,
    filterText,
    count,
    active = false) => {
    return `<input
			type="radio"
			id="filter__${filterId}"
			class="filter__input visually-hidden"
			name="filter"
			${active ? 'checked' : ''}
			${count ? '' : 'disabled'} />
		<label for="filter__${filterId}" class="filter__label">
			${filterText} <span class="filter__${filterId}-count">${count}</span></label
		>`;
  };

  return `<section class="main__filter filter container">
			${createFiltersItemTemplate('all', 'All', 14, true)}
			${createFiltersItemTemplate('overdue', 'Overdue', 0)}
			${createFiltersItemTemplate('today', 'Today', 0)}
			${createFiltersItemTemplate('favorites', 'Favorites', 1)}
			${createFiltersItemTemplate('repeating', 'Repeating', 1)}
			${createFiltersItemTemplate('archive', 'Archive', 115)}
		</section>
		`;
};

//Sorting

const createSortComponent = () => {
  const createSortItem = (text, sortType) => {
    return `<a href="#" class="board__filter" data-sort-type=${sortType}>${text}</a>`;
  };

  return `<div class="board__filter-list">
			${createSortItem('SORT BY DEFAULT', 'default')}
			${createSortItem('SORT BY DATE up', 'date-up')}
			${createSortItem('SORT BY DATE down', 'date-down')}
			
		</div>`;
};

//Форма создания/редактирования задачи (используется одна форма);
const createAddEditTaskForm = (
  taskText = 'Here is a card with filled data',
  date,
  isStatusDateYes = false,
  isStatusRepeatYes = false) => {
  return `form class="card__form" method="get">
		<div class="card__inner">
			<div class="card__color-bar">
				<svg class="card__color-bar-wave" width="100%" height="10">
					<use xlink:href="#wave"></use>
				</svg>
			</div>

			<div class="card__textarea-wrap">
				<label>
					<textarea
						class="card__text"
						placeholder="Start typing your text here..."
						name="text"
					>${taskText}</textarea>
				</label>
			</div>

			<div class="card__settings">
				<div class="card__details">
					<div class="card__dates">
						<button class="card__date-deadline-toggle" type="button">
							date: <span class="card__date-status">${isStatusDateYes ? 'yes' : 'no'}</span>
						</button>

						<fieldset class="card__date-deadline" ${isStatusDateYes ? '' : 'disabled'}>
							<label class="card__input-deadline-wrap">
								<input
									class="card__date"
									type="text"
									placeholder=""
									name="date"
									value="${date}"
								/>
							</label>
						</fieldset>

						<button class="card__repeat-toggle" type="button">
							repeat:<span class="card__repeat-status">${isStatusRepeatYes ? 'yes' : 'no'}</span>
						</button>

						<fieldset class="card__repeat-days" ${isStatusRepeatYes ? '' : 'disabled'}>
							<div class="card__repeat-days-inner">
								<input
									class="visually-hidden card__repeat-day-input"
									type="checkbox"
									id="repeat-mo-4"
									name="repeat"
									value="mo"
								/>
								<label class="card__repeat-day" for="repeat-mo-4"
									>mo</label
								>
								<input
									class="visually-hidden card__repeat-day-input"
									type="checkbox"
									id="repeat-tu-4"
									name="repeat"
									value="tu"
									checked
								/>
								<label class="card__repeat-day" for="repeat-tu-4"
									>tu</label
								>
								<input
									class="visually-hidden card__repeat-day-input"
									type="checkbox"
									id="repeat-we-4"
									name="repeat"
									value="we"
								/>
								<label class="card__repeat-day" for="repeat-we-4"
									>we</label
								>
								<input
									class="visually-hidden card__repeat-day-input"
									type="checkbox"
									id="repeat-th-4"
									name="repeat"
									value="th"
								/>
								<label class="card__repeat-day" for="repeat-th-4"
									>th</label
								>
								<input
									class="visually-hidden card__repeat-day-input"
									type="checkbox"
									id="repeat-fr-4"
									name="repeat"
									value="fr"
									checked
								/>
								<label class="card__repeat-day" for="repeat-fr-4"
									>fr</label
								>
								<input
									class="visually-hidden card__repeat-day-input"
									type="checkbox"
									name="repeat"
									value="sa"
									id="repeat-sa-4"
								/>
								<label class="card__repeat-day" for="repeat-sa-4"
									>sa</label
								>
								<input
									class="visually-hidden card__repeat-day-input"
									type="checkbox"
									id="repeat-su-4"
									name="repeat"
									value="su"
									checked
								/>
								<label class="card__repeat-day" for="repeat-su-4"
									>su</label
								>
							</div>
						</fieldset>
					</div>
				</div>

				<div class="card__colors-inner">
					<h3 class="card__colors-title">Color</h3>
					<div class="card__colors-wrap">
						<input
							type="radio"
							id="color-black-4"
							class="card__color-input card__color-input--black visually-hidden"
							name="color"
							value="black"
						/>
						<label
							for="color-black-4"
							class="card__color card__color--black"
							>black</label
						>
						<input
							type="radio"
							id="color-yellow-4"
							class="card__color-input card__color-input--yellow visually-hidden"
							name="color"
							value="yellow"
							checked
						/>
						<label
							for="color-yellow-4"
							class="card__color card__color--yellow"
							>yellow</label
						>
						<input
							type="radio"
							id="color-blue-4"
							class="card__color-input card__color-input--blue visually-hidden"
							name="color"
							value="blue"
						/>
						<label
							for="color-blue-4"
							class="card__color card__color--blue"
							>blue</label
						>
						<input
							type="radio"
							id="color-green-4"
							class="card__color-input card__color-input--green visually-hidden"
							name="color"
							value="green"
						/>
						<label
							for="color-green-4"
							class="card__color card__color--green"
							>green</label
						>
						<input
							type="radio"
							id="color-pink-4"
							class="card__color-input card__color-input--pink visually-hidden"
							name="color"
							value="pink"
						/>
						<label
							for="color-pink-4"
							class="card__color card__color--pink"
							>pink</label
						>
					</div>
				</div>
			</div>

			<div class="card__status-btns">
				<button class="card__save" type="submit">save</button>
				<button class="card__delete" type="button">delete</button>
			</div>
		</div>
	</form>`;
};

//Карточка задачи
const createTaskCard = (color, isEdit = false) => {
  const cardTemplate = () => {
    return `<div class="card__form">
			<div class="card__inner">
				<div class="card__control">
					<button type="button" class="card__btn card__btn--edit">
						edit
					</button>
					<button type="button" class="card__btn card__btn--archive">
						archive
					</button>
					<button
						type="button"
						class="card__btn card__btn--favorites card__btn--disabled"
					>
						favorites
					</button>
				</div>

				<div class="card__color-bar">
					<svg class="card__color-bar-wave" width="100%" height="10">
						<use xlink:href="#wave"></use>
					</svg>
				</div>

				<div class="card__textarea-wrap">
					<p class="card__text">Example task with default color.</p>
				</div>

				<div class="card__settings">
					<div class="card__details">
						<div class="card__dates">
							<div class="card__date-deadline">
								<p class="card__input-deadline-wrap">
									<span class="card__date">23 September</span>
									<span class="card__time">16:15</span>
								</p>
							</div>
						</div>
					</div>
				</div>
			</div>
		</div>`;
  };

  return `<article class="card ${
    isEdit ? 'card--edit card--repeat' : ''} card--${color} ">
		${isEdit ? createAddEditTaskForm(
    'Новая задача. Вы можете установить дату и время',
    '24 Сентября 14:23'): cardTemplate()}
	</article>`;
};

// button 'load more'
const createLoadMoreElement = () => {
  // eslint-disable-next-line quotes
  return (`<button class="load-more" type="button">load more</button>`);
};

//Board component
const createBoardComponent = () => {
  return `<section class="board container">
			${createSortComponent()}
			<div class="board__tasks">
			${createTaskCard('black', true)}
			${createTaskCard('blue')}
			${createTaskCard('yellow')}
			${createTaskCard('green')}
			</div>
			${createLoadMoreElement()}
		</section>`;
};

const mainElement = document.querySelector('.main');
const mainControl = mainElement.querySelector('.main__control');
renderElement(mainControl, createMenu());
renderElement(mainElement, createFilters());
renderElement(mainElement, createBoardComponent());
